$grey: #888;
$lighter: #eee;

.project-card {
    // border-radius: 3px
    border-top: 1px solid $grey;
    padding: 1rem 0;
    margin: 0 auto;
    // margin-bottom: 0
    // box-shadow: 0px -3px 3px 3px $grey

    header {
       margin-top: 1rem;
       display: flex;
       flex-wrap: wrap;
       justify-content: center;

       a, .project-type-icon {
        opacity: 0.7;
        transition: opacity 0.5s;
        margin-left: 1rem;
        svg {
           transition: transform 0.3s; }

        &:hover {
          opacity: 1;
          svg {
           transform: scale(1.2); } } }


       .image-wrapper {
          margin-right: 1rem;
          margin-bottom: 1rem;
          background-color: $lighter;
          height: 160px;
          cursor: pointer;
          position: relative;
          transition: all 0.5s;
          filter: grayscale(1);
          &:hover {
            transform: scale(1.05);
            z-index: 2;
            filter: grayscale(0); }

          img {
           width: 240px;
           height: 160px;
           object-fit: contain;
           max-width: 100%; }

          .zoom-icon {
            position: absolute;
            bottom: 0.5rem;
            right: 0.5rem;
            opacity: 0.5;
            transition: opacity 0.5s;
            &:hover {
              opacity: 2; } } }


       .project-content {
         display: flex;
         flex-direction: column;
         flex: 1 1 20em;

         h2 {
           margin: 0;
           display: flex;
           justify-content: space-between;
           cursor: pointer; }
         .bottom-buttons {
           display: flex;
           justify-content: space-between;
           flex-grow: 1;
           align-items: flex-end;
           margin-bottom: 1rem; } } }

    .extended {
      transition: all 1s; }
    .extended.hidden {
      height: 0;
      overflow-y: hidden; } }

