body {
 margin: 0 1rem;
 font-family: 'Roboto', sans-serif;
 transition: all 1s; }

//  font-family: 'Open Sans', sans-serif

*:focus {
 outline: none; }

.dark {
    color: #ddd;
    background-color: black; }
a {
 color: inherit;
 text-decoration: none; }

button {
 border: 1px solid grey;
 background: none;
 border-radius: 3px;
 padding: 0.5rem;
 color: inherit;
 cursor: pointer; }

.App {
    max-width: 900px;
    margin: 3rem auto; }
